export default {
    props: {
        placeholder: {
            type: String,
            default: '手机号/微信昵称/姓名'
        },
        bgStyle: {
            type: String,
            default: ''
        },
        inputStyle: {
            type: String,
            default: ''
        },
        disabled: {
            type: Boolean,
            default: false
        },
        value:{
            type:String, 
            default:''
        }
    },
    watch:{
        value(){
            this.val=this.value; 
        }
    },
    data() {
        return {
            val: ''
        }
    },
    mounted(){
        this.val=this.value; 
    },
    computed: {
        showClear() {
            return this.val !== '';
        }
    },
    methods: {
        getInput(val) {
            if (val == 'clear') {
                this.val = '';
                this.$emit('clear') 
            } else {
                this.val = val.detail.value;
                this.$emit(val.type, val.detail)
            }
        },
        clickSearch(){
            this.$emit('click',this.val)
        }
    },
}
